<script>

export default {
  name: 'noticeList',
  props: {
  },
  data: () => {
    return {
      books: []
    }
  },
  mounted () {
    const t = this;
    t.books= t.$store.state.booksByIsbn;
  },
  created () {
  },
  methods: {
    chkAuthorCde() {
      let isAllChk = true;
      const bookList = document.querySelectorAll('[name="bookWithoutAuthorCde"]');
      const allChk = document.querySelector('[name="booksWithoutAuthorCde"]');
      bookList.forEach((el) => {
        if(!el.checked){
          isAllChk = false;
          return false;
        }
      });

      allChk.checked = isAllChk;
    },

    chkAllAuthorCde() {
      const allChk = document.querySelector('[name="booksWithoutAuthorCde"]');
      const bookList = document.querySelectorAll('[name="bookWithoutAuthorCde"]');
      bookList.forEach((el) => {el.checked = allChk.checked;});

    }
  }
}
