<script>

export default {
  name: 'noticeList',
  props: {
  },
  data: () => {
    return {
      bookCde: '',
      bookInfo: {},
      initBookInfo: {}, //나중에 사용
      editabled: false,
      returnEditabled: false,
      members: [],
      searchType: 'monthly',
    }
  },
  watch: {
    searchType: function(newVal, oldVal) {
      const T = this;
      T.getMembersByRentBook();
    },
  },
  mounted () {
    const T = this;
    T.bookCde = T.$store.state.ref.libMng.popup.data.bookCde;

    T.getBookInfo();
    T.getMembersByRentBook();
  },
  created () {
  },
  methods: {
    chkAuthorCde() {
      let isAllChk = true;
      const bookList = document.querySelectorAll('[name="bookWithoutAuthorCde"]');
      const allChk = document.querySelector('[name="booksWithoutAuthorCde"]');
      bookList.forEach((el) => {
        if(!el.checked){
          isAllChk = false;
          return false;
        }
      });

      allChk.checked = isAllChk;
    },

    chkAllAuthorCde() {
      const allChk = document.querySelector('[name="booksWithoutAuthorCde"]');
      const bookList = document.querySelectorAll('[name="bookWithoutAuthorCde"]');
      bookList.forEach((el) => {el.checked = allChk.checked;});
    },

    getBookInfo() {
      const T = this;
      T.$axios.post(`${T.$store.state.serverUrl}/getBookInfo`, {bookCde: T.bookCde}).then((res) => {
        T.bookInfo = res.data[0];

        console.log(T.bookInfo);
      });
    },

    getMembersByRentBook() {
      const T = this;
      T.$axios.post(`${T.$store.state.serverUrl}/getMembersByRentBook`, {bookCde: T.bookCde, type: T.searchType}).then((res) => {
        console.log(res);
        T.members = res.data;
      });

    },
  }
}
</script>

