<template>
  <header>
    <div ref="headerTop" class="header-top cf responsive-width mg0auto">
      <div class="float-r">
        <strong class="mp0 mg-r10">로그인</strong>
        <strong class="mp0">회원가입</strong>
      </div>
    </div>
    <div ref="headerBottom" class="header-bottom cf responsive-width mg0auto">
      <!-- <h1 class="mg0 float-l pointer">
        <router-link :to="{path: '/'}">
          <img src="../assets/img/logo1.svg" alt="로고" height="50" class="v-top">
        </router-link>
      </h1> -->
      
      <h2 class="hamburger" @click="toggleMenu">
          <img src="../assets/img/hamburger.svg" alt="메뉴" height="30" class="v-top">
      </h2>
      <nav>
        <div class="close-menu-button-wrapper">
          <h2 class="close-menu">
              <img src="../assets/img/btn_x2.svg" alt="메뉴" @click="toggleMenu" height="20" class="pointer">
          </h2>
        </div>
        <div class="mobile-login-section">
          <button class="mg-r5">로그인</button>
          <button>회원가입</button>
        </div>
        <ul class="mp0 cf">
          <li @mouseenter="setMenu_on($event);" @mouseleave="setMenu_off($event);" @click="clickMenu($event);">
            <strong class="inline-block">도서관정보</strong>
            <ul>
              <li><span>도서관소개</span></li>
              <li><span>이용안내</span></li>
              <li><span>오시는길</span></li>
            </ul>
          </li>
          <li @mouseenter="setMenu_on($event);" @mouseleave="setMenu_off($event);" @click="clickMenu($event);">
            <strong strong class="inline-block">도서관련</strong>
            <ul>
              <li><span>도서검색</span></li>
              <li><span>도서신청</span></li>
              <li><span>신간도서</span></li>
            </ul>
          </li>
          <li @mouseenter="setMenu_on($event);" @mouseleave="setMenu_off($event);" @click="clickMenu($event);">
            <strong strong class="inline-block">열린마당</strong>
            <ul>
              <li><router-link :to="{path: '/noticeList', query: {page: 1}}"><span>공지사항</span></router-link></li>
              <li><span>건의사항</span></li>
              <li><span>도서관 활동</span></li>
            </ul>
          </li>
          <li @mouseenter="setMenu_on($event);" @mouseleave="setMenu_off($event);" @click="clickMenu($event);">
            <strong strong class="inline-block">나눔터</strong>
            <ul>
              <li><span>물품나눔</span></li>
              <li><span>재능나눔</span></li>
            </ul>
          </li>
          <li v-if="$store.state.windowWidth >= 860">
            <strong strong class="inline-block" @click="goToLibMng">도서관관리</strong>
          </li>
        </ul>
      </nav>
    </div>
    <div ref="menuBg" class="menu-bg" @click="toggleMenu"></div>
  </header>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  methods: {
    setMenu_on(target) {
      const T = this;
      const WINDOW_WIDTH = T.$store.state.windowWidth;
      if(WINDOW_WIDTH <= 860) return;
      const ul = target.target.closest('ul');
      const uls = ul.querySelectorAll(':scope ul');
      uls.forEach((el) => {el.classList.add('on');});
      this.$refs.menuBg.classList.add('on');
      this.$refs.headerBottom.classList.add('on');
    },
    setMenu_off(target) {
      const T = this;
      const WINDOW_WIDTH = T.$store.state.windowWidth;
      if(WINDOW_WIDTH <= 860) return;
      const ul = target.target.closest('ul');
      const uls = ul.querySelectorAll(':scope ul');
      uls.forEach((el) => {el.classList.remove('on');});
      this.$refs.menuBg.classList.remove('on');
      this.$refs.headerBottom.classList.remove('on');
    },
    goToLibMng(target){
      const t = this;
      if(false){ //로그인 + 관리자 권한
        alert();
        return;
      }
      t.$router.push({ name: 'libMng'})
    },
    toggleMenu(bool){ //bool이 true일 경우 메뉴 열림
      const nav = document.querySelector('nav');
      if(typeof bool !== 'object'){
        if(bool){
          nav.classList.add('on');
          this.$refs.menuBg.classList.add('on');
        }else{
          nav.classList.remove('on');
          this.$refs.menuBg.classList.remove('on');
        }
        return;
      }
      nav.classList.toggle('on');
      this.$refs.menuBg.classList.toggle('on');
    },
    clickMenu(target){
      const TARGET_TAG = (target.target);
      const LI = TARGET_TAG.closest('li');
      const SUB_MENU = LI ? LI.querySelector('ul') : TARGET_TAG.querySelctor('ul');

      console.log(SUB_MENU);
      SUB_MENU.classList.toggle('on');
    }
  },
  mounted() {
    const t = this;
    t.$store.state.ref.header = t;
  }
}
</script>

