  ul{list-style:none;}
  nav > ul > li{float:left; width:100px; cursor:pointer; text-align:center;}
  nav li li:hover span{background:#74bb2a; color:#fff; font-weight:900; transition:all .5s;}
  nav ul ul{margin:0; padding:0; position:relative; top:10px; display:none;}
  nav ul ul.on{display:block;}
  a{color:#000; text-decoration:none;}
  .menu-bg{position:absolute; bottom:-150px; width:100%; height:151px; background:linear-gradient(to top, #0000001a 1px, #fff 0); display:none;}
  .menu-bg.on{display:block;}


  @media (max-width:860px){
    header{background: 0;}
    nav{position:absolute; height:calc(100vh); background:#fff; top: 0; right:-250px; padding: 0 10px; overflow-y:auto; transition:all .3s ease-in-out;}
    nav.on{right:0;}
    nav > ul > li{float:none; padding:5px; border-bottom:1px solid #0000001a; width:200px;}
    .menu-bg{background: #00000080; width: 100vw; height: 100vh; display: block; top: 0; bottom: initial; right: -100vw; transition: all 0 linear;}
    .menu-bg.on{right:0;}
    nav ul ul{display:none; top:0; background:#0000001a; margin-bottom:20px;}
    nav ul ul.on{display:block;}

    /* --------------------- */

     .hamburger{display: block;}
     .close-menu-button-wrapper{display: block;}
     .mobile-login-section{display: block;}

     .header-top{display: none;}
     header{position: fixed; top: 0; right: 0;}
