<template>
  <div class="h100p cf border-box">
    <section class="">
      <header>
        <div class="mg-b5">
          <select type="text" ref="searchType" class="w120 h40 border-box v-top border-aaa mg-r5">
            <!-- <option value="all">전체</option> -->
            <option value="cde">도서코드</option>
            <option value="title">제목</option>
            <option value="author">저자</option>
            <option value="pub">출판사</option>
          </select>
          <input type="text" ref="searchValue" class="mg-r5 w300 h40 border-box v-top border-aaa pd10" @keyup.enter="getBookList(10, 1)">
          <button class="outline0 border0 h40 line-h40px w120 v-top color-fff pointer" style="background:#74bb2a;" @click="getBookList(10, 1)">검색</button>
        </div>
        <div class="cf">
          <div class="tab-menu float-l">
            <label class="pointer">
              <input type="radio" name="bookFilter" value="all" class="none" checked v-model="bookFilter" />
              <strong class="txt-c inline-block v-top bg-ddd mg-r5 pd10 w120 border-box">전체</strong>
            </label>
            <label class="pointer">
              <input type="radio" name="bookFilter" value="rent" class="none" v-model="bookFilter"/>
              <strong class="txt-c inline-block v-top bg-ddd mg-r5 pd10 w120 border-box">대여중</strong>
            </label>
            <label class="pointer">
              <input type="radio" name="bookFilter" value="overdue" class="none" v-model="bookFilter"/>
              <strong class="txt-c inline-block v-top bg-ddd pd10 w120 border-box">연체중</strong>
            </label>
          </div>
          <div class="float-r h40 line-h40px" v-if="books.totalCnt">
            <!-- <strong>{{bookList[bookFilter].length}}건</strong> -->
            <strong>{{books.totalCnt}}건</strong>
          </div>
        </div>
      </header>
      <div style="border-top:1px dashed #aaa" class="mg-t5"></div>
      <ul class="book-list list-style-none mp0 overflow-y-auto">
        <li class="border-b-ccc pd5 cf pointer" v-for="(item, idx) in books.list" :key="idx" @click="openPopup(item.book_cde);">
          <div class="float-l w100p-180">
            <div class="mg-b5 txt-overflow">
              <strong class="mg-r5">[{{item.book_cde}}]</strong> 
              <span v-if="item.series_title">{{item.series_title}} / </span><span>{{item.title}}</span>
            </div>
            <div class="txt-overflow">
              <span>{{item.author_info ? item.author_info : '-'}}</span> |  <span>{{item.pub_info ? item.pub_info : '-'}}</span>
            </div>
          </div>
          <div class="float-l w180">
            <div class="mg-b5 txt-r">
              <strong class="color-green" v-if="item.rent_dt && !item.return_dt && new Date(today) <= new Date(item.should_return_dt)">
                대여중 <span class="color-000">({{item.mbr_nm}})</span>
              </strong>
              <strong class="color-red" v-else-if="item.rent_dt && !item.return_dt && new Date(today) > new Date(item.should_return_dt)">
                연체중 <span class="color-000">({{item.mbr_nm}})</span>
              </strong>
              <strong v-else>-</strong>
            </div>
            <div class="txt-r">
              <strong class="color-42c7d5 mg-r5">[{{item.class_no}}]</strong><strong>{{item.author_cde}}</strong>
            </div>
          </div>
        </li>
      </ul>
      <ul class="pagenation" v-if="books.totalCnt > pageControl.viewListCnt">
        <li v-if="pageControl.pageGroupPosition > 1"
        @click="getBookList(10, ((pageControl.pageGroupPosition - 2) * pageControl.viewPageCnt) + 1);">
          <strong>&lt;</strong>
        </li>
        <li v-for="(idx) in pageControl.pageGroupPosition < pageControl.pageGroupCnt ? pageControl.viewPageCnt : pageControl.pageCnt % pageControl.viewPageCnt == 0 ? pageControl.viewPageCnt :  pageControl.pageCnt % pageControl.viewPageCnt" :key="idx">
          <label>
            <input name="pagePosition" v-model="pageControl.pagePosition" type="radio" :value="((pageControl.pageGroupPosition - 1) * pageControl.viewPageCnt) + idx">
            <strong>{{((pageControl.pageGroupPosition - 1) * pageControl.viewPageCnt) + idx}}</strong>
          </label>
        </li>
        <li v-if="pageControl.pageGroupPosition < pageControl.pageGroupCnt"
        @click="getBookList(10, (pageControl.pageGroupPosition * pageControl.viewPageCnt) + 1);">
          <strong>&gt;</strong>
        </li>
      </ul>
    </section>
  </div>
</template>

<script>

export default {
  name: 'noticeList',
  props: {
    msg: String
  },
  data: () => {
    return {
      files: [],
      contentOpt: true,
      bookFilter: 'all',
      bookList2: {
        all : [],
        rent : [],
        overdue : [],
        listByPage: [],
      },
      books: {
        list: [],
        totalCnt: 0
      },
      today: '0000-00-00',
      pageControl: {
        pagePosition: 1,
        pageGroupPosition: 1,
        pageCnt: 1,
        pageGroupCnt: 1,
        viewListCnt: 1,
        viewPageCnt: 1
      },
    }
  },
  watch: {
    bookFilter: function(newVal, oldVal) {
      const T = this;
      T.getBookList(10, 1);
      // const T = this;
      // T.bookList.listByPage = [];
      // T.bookList[newVal].forEach((el) => {
      //   if(T.bookList.listByPage.length < 10){
      //     T.bookList.listByPage.push(el);
      //   }
      // });

      // T.pageControl = T.$calcPage({
      //   totalListCnt: T.bookList[T.bookFilter].length,
      //   viewListCnt: 10,
      //   viewPageCnt: 10,
      // });
      // if(document.querySelectorAll('input[name="pagePosition"]').length){
      //   document.querySelectorAll('input[name="pagePosition"]')[0].checked = true;
      // }

    },

    'pageControl.pagePosition': function(newVal, oldVal){
      const T = this;
      // T.bookList.listByPage = [];
      // T.$setGroupByNum(newVal, 10).forEach((el) => {
      //   if(T.bookList[T.bookFilter][el]){
      //     T.bookList.listByPage.push(T.bookList[T.bookFilter][el]);
      //   }
      // })
      T.getBookList(10, newVal);
    }
  },
  mounted () {
    const T = this;
    const DATE = new Date();
    T.today = DATE.toISOString().substr(0, 10); //T전까지
  },
  created () {
  },
  methods: {
    getBookList2(listCnt){
      const T = this;
      T.bookList.listByPage = [];
      T.$axios.get(`${T.$store.state.serverUrl}/getBookList?type=${T.$refs.searchType.value}&value=${T.$refs.searchValue.value}`).then((res) => {
        T.bookList.all = res.data;
        T.bookList.rent = [];
        T.bookList.overdue = [];
        T.bookList.all.forEach((el, idx) => {
          if(el.rent_dt && !el.return_dt && new Date(T.today) <= new Date(el.should_return_dt)){
            T.bookList.rent.push(el);
          }else if(el.rent_dt && !el.return_dt && new Date(T.today) > new Date(el.should_return_dt)){
            T.bookList.rent.push(el);
            T.bookList.overdue.push(el);
          }
        });

        T.bookList[T.bookFilter].forEach((el, idx) => {
          if(idx < listCnt) T.bookList.listByPage.push(el);
        });

        //pagenation
        T.pageControl = T.$calcPage({
          totalListCnt: T.bookList[T.bookFilter].length,
          viewListCnt: 10,
          viewPageCnt: 10,
        });
        T.$nextTick(() => {
          if(document.querySelectorAll('input[name="pagePosition"]').length){
            document.querySelectorAll('input[name="pagePosition"]')[0].checked = true;
          }
        })
      });
    },
    getBookList(listCnt, offset){
      const T = this;
      // console.log(offset);
      const calcOffset = listCnt * ((offset ? offset : 1) - 1);
      T.$axios.get(`${T.$store.state.serverUrl}/getBookList?bookFilter=${T.bookFilter}&type=${T.$refs.searchType.value}&value=${T.$refs.searchValue.value}&cnt=${listCnt}&offset=${calcOffset}`).then((res) => {
        T.books = res.data;
        T.pageControl = T.$initPageControl(T.books.totalCnt, 10, 10, offset);

        // console.log(T.pageControl);
      });
    },

    openPopup(bookCde) {
      const T = this;
      T.$store.state.ref.libMng.popup.view = 'mngBookPop';
      T.$store.state.ref.libMng.popup.data = {};
      T.$store.state.ref.libMng.popup.data['bookCde'] = bookCde;
    },


  }
}
</script>

