  .tab-menu label input:checked + strong{background:#74bb2a; color:#fff;}

  .book-list > li:hover{background:#eef8ee; transition:all .5s;}

  .pagenation{list-style:none; text-align:center;}
  .pagenation li{display:inline-block; margin-right:10px;}
  .pagenation li label{width:50px; height:50px; display:inline-block;}
  .pagenation li input{display:none;}
  .pagenation li input:checked + strong{background:#74bb2a; color:#fff;}
  .pagenation li strong{display:inline-block; width:50px; height:50px; border:1px solid #aaa; background:#fff;  line-height:50px; cursor:pointer;}
</style>
